import React, { useState } from 'react';
import cls from './style.module.scss';
import ProfileStatus from './ProfileStatus';

function ProfileInfo(props) {
	let [editMode, setEditMode] = useState(false);
	let [isOwner, setIsOwner] = useState(true);

	return (
		<div className={cls.profile__info}>
			<div
				className={cls.avatar}
				style={{
					backgroundImage: `url(${
						props.profile.photos.large != null
							? props.profile.photos.large
							: 'data:image/png;base64,'
					})`,
				}}
			/>

			{editMode ? (
				<form className={cls.info}>
					{isOwner && (
						<button
							onClick={() => {
								setEditMode(false);
							}}
						>
							Save
						</button>
					)}
					<p className={cls.name}>
						<input value={props.profile.fullName} />
					</p>
					<ProfileStatus status={props.status} updateStatus={props.updateStatus} />
					<div className={`line ${cls.line}`}>
						<p>Looking for a job : </p>
						<p>
							<input value={props.profile.lookingForAJob ? ` yes` : `no`} />
							<input
								value={
									props.profile.lookingForAJobDescription
										? props.profile.lookingForAJobDescription
										: `no description`
								}
							/>
						</p>
					</div>
					<div className={`line ${cls.line}`}>
						<p>Contact</p>
					</div>

					{Object.keys(props.profile.contacts).map(key => (
						<div key={key} className={`line ${cls.line}`}>
							<p>{key}:</p>
							<input value={props.profile[key]} />
						</div>
					))}
				</form>
			) : (
				<div className={cls.info}>
					{isOwner && (
						<button
							onClick={() => {
								setEditMode(true);
							}}
						>
							Edit my info
						</button>
					)}
					<p className={cls.name}>{props.profile.fullName}</p>
					<ProfileStatus status={props.status} updateStatus={props.updateStatus} />
					<div className={`line ${cls.line}`}>
						<p>Looking for a job : </p>
						<p>
							{props.profile.lookingForAJob ? ` yes ` : 'no'}
							<br />
						</p>
					</div>
					<div className={`line ${cls.line}`}>
						{props.profile.lookingForAJob ? (
							<p>
								{props.profile.lookingForAJobDescription
									? props.profile.lookingForAJobDescription
									: `no description`}
							</p>
						) : null}
					</div>
					<div className={`line ${cls.line}`}>
						<p>Contact</p>
					</div>

					{Object.keys(props.profile.contacts).map(key => (
						<div key={key} className={`line ${cls.line}`}>
							<p>{key}:</p>
							<p>{props.profile[key]}</p>
						</div>
					))}
				</div>
			)}
		</div>
	);
}

export default ProfileInfo;
